                            <td width="25%" align="center" class="color58e">{{item.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{item.operatePlace}}</td>
                            <td width="25%" align="center" class="colorred">{{item.money}}</td>
                        </tr>
                        <tr height="40px" class="fontsize">
                            <td width="25%" align="center" class="color9">{{item.operateDate}}</td>
                            <td width="25%" align="center" class="color58e">{{item.operateType}}</td>
                            <td width="25%" align="center" class="colorgreen">{{item.operatePlace}}</td>
