            </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'detaillist',
  data () {
    return {
      msg: '缴存明细',
      usermsg:''
    }
  },
  created(){
        // var idnumber = this.$store.idnumber
        // var name = this.$store.name
        // var name = this.$store.name
        // var password = this.$store.password
        var realname = localStorage.getItem("realname")
        var cardid = localStorage.getItem("cardid")

         this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/commonaccumulationfunddetail.do', {
            params: {
                idnumber:"1593579582666",
                name:"王超"//这里是登录后的姓名
            }
        })
          .then( res => {
            var data = res.data
            this.usermsg = data
        })
          .catch(function (error) {
          console.log(error);
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
