        color: #999;
    }
    .balance span:nth-of-type(2) {
        color: #999;
    }
    .balance {
        height: 22px;
        padding: 10px;
        padding-left: 12px;
        border-bottom: 1px solid #e7e7e7;
    }
  /* 明细 */
