        /* margin-top: 10px; */
    }
    .font_bgc {
        font-size: 12px;
        background-color: #e7e7e7;
    }
    .font14 {
        font-size: 14px;
    }
    .fontsize {
        font-size: 12px;
    }
    .color9 {
        color: #999;
    }
    .color58e {
        color: #00558e;
    }
    .colorgreen {
        color: #259b24;
    }
    .colorred {
        color: red;
    }
    .table_title {
      width: 100%;
      padding-left: 12px;
      background-color: #e7e7e7;
      height: 30px;
      line-height: 30px;
      box-sizing: border-box;
    }
    .right {
        float: right;
    }
    
</style>



