<template>
  <div id="app" v-if="isRouterAlive">
    
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  provide(){
    return{
      reload:this.reload
    }
  },
  data(){
    return{
      isRouterAlive:true
    }
  },
  methods:{
    reload(){
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  /* padding-top: 60px; */
}
body {
  margin: 0;
}
.footer_fixed {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #f8f8f8;
  position: fixed;
  bottom: 0;
  font-size: 12px;
  color: #999;
}
</style>



