<template>
<div>
  <div class="security_detail" v-for="(item,index) in usermsg" :key="index">
      <!-- header部分 -->
    <header>
        <div class="h_content">
            <div class="user">
                <span>
                    {{item.name}}的账户
                </span>
            </div>
            <div class="h_content_left">
                <div id="int_result" v-show="isshow">{{item.balance}}</div>
                <div id="hide" v-show="isshow2">****</div>
                <img src="../assets/open.png" alt="" v-show="isshow" @click="toggle()">
                <img src="../assets/close.png" alt="" v-show="isshow2" @click="toggle()">
                <div class="balance">账户余额(元)</div>
            </div>
            <div class="h_content_right">
                <span>{{item.monthes}}</span>
                <span>已缴纳月数</span>
            </div>
        </div>
    </header>
    <!-- 个人信息部分 -->
    <div class="usermsg">
        <div class="msg height30 borderleft">
            <span>基本信息</span>
            <span class="right">{{item.status}}</span>
        </div>
        <div class="msg1 border height font14">
            <span>公积金账号</span>
            <span class="right">{{item.account}}</span>
        </div>
        <div class="msg2 height font14">
            <span>姓名</span>
            <span class="right">{{item.name}}</span>
        </div>
        <div class="msg3 height font14">
            <span>身份证号</span>
            <span class="right">{{item.idNumber}}</span>
        </div>
        <div class="msg4 height font14">
           <span>关联银行卡号</span>
           <span class="right">{{item.bankCard}}</span>
        </div>
    </div>
    <div class="usermsg">
        <div class="msg height30 borderleft">
            <span>缴存信息</span>
            <router-link to="/detaillist" class="right">明细></router-link>
        </div>
        <div class="msg1 border height font14">
            <span>单位缴存</span>
            <span class="right">{{item.companyPay}}</span>
        </div>
        <div class="msg2 height font14">
            <span>个人缴存</span>
            <span class="right">{{item.personalPay}}</span>
        </div>
        <div class="msg3 height font14">
            <span>最近存储日期</span>
            <span class="right">{{item.lastDate}}</span>
        </div>
    </div>
</div>
</div>
</template>

<script>

import Vue from 'vue'
import { Navbar, TabItem } from 'mint-ui';
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);

export default {
  name: 'security_detail',
  data () {
    return {
      msg: '社保明细',
      isshow:true,
      isshow2:false,
      selected:'1',
      show:false,
      usermsg:''
    }
  },
  methods:{
        toggle() {
            this.isshow = !this.isshow
            this.isshow2 = !this.isshow2
        },
        showtime() {
            this.show = !this.show
            var showimg = document.getElementById("showimg")
            if(this.show == true) {
                showimg.classList.add("toggleimg");
            }else {
                showimg.classList.remove("toggleimg");
            }
        }
    },
    created(){
        var cityname = this.$store.cityname
        // var name = this.$store.name
        // var password = this.$store.password
        var realname = localStorage.getItem("realname")
        var cardid = localStorage.getItem("cardid")
        // alert(realname)
        // alert(cardid)
         this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/commonaccumulationfund.do', {
            params: {
                // location:cityname,
                // idnumber:realname, //这是传过来的姓名
                // password:cardid//这里当初的密码输入框input值，也是现在的身份证号

                location:"南京",
                idnumber:"王超", //这是传过来的姓名
                password:"1593579582666"//这里当初的密码输入框input值，也是现在的身份证号
            }
        })
          .then( res => {
            var data = res.data
            this.usermsg = data
        })
          .catch(function (error) {
          console.log(error);
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
    /* header 部分 */
    header {
        width: 100%;
        height: 136px;
        background: url(../assets/ss-bg.png) no-repeat;
        background-size: 100%;
        padding: 15px 10px 0px 10px;
        box-sizing: border-box;
    }
    .h_content {
        box-sizing: border-box;
        width: 100%;
        padding: 10px 10px 30px;
        color: #fff;
    }
    .h_content_left {
        width: 50%;
        position: relative;
        border-right: 1px solid #fff;
        margin: 0;
        float: left;
    }
    .h_content img {
        width: 25px;
        height: 25px;
        position: absolute;
        top: 6%;
        right: 6%;
    }
    #int_result {
        width: 60px;
        height: 40px;
        box-sizing: border-box;
        font-size: 22px;
    }
    #hide {
        font-size: 35px;
        text-align: left;
        width: 60px;
        height: 40px;
    }
    .balance {
        font-size: 14px;
        color: rgba(255, 255, 255, .7);
    }

    .h_content_right {
        width: 45%;
        float: right;
        text-align: center;
        float: right;
    }
    .h_content_right span {
        display: block;
        margin: 5px 0 10px 0;
    }
    .h_content_right span:nth-of-type(1) {
        font-size: 22px;
    }
    .h_content_right span:nth-of-type(2) {
        font-size: 14px;
        color: rgba(255, 255, 255, .7);
    }
    .user {
        width: 100%;
        height: 40px;
        text-align: center;
    }
    /* 个人信息部分 */
    .usermsg {
        width: 100%;
        height: 160px;
        margin-top: 40px;
        padding: 10px;
        box-sizing: border-box;
    }
    .height30 {
        height: 30px;
        line-height: 30px;
    }
    .height {
        height: 35px;
        line-height: 35px;
    }
    .border {
        border-top: 1px solid #e7e7e7;
    }
    .borderleft {
        border-left: 5px solid #00558E;
    }
    .right {
        float: right;
    }
    .font14 {
        font-size: 14px;
        color: #999;
    }
    .msg {
        padding-left: 3%;
        margin-bottom: 10px;
    }
    .msg span:nth-of-type(2) {
        font-size: 12px;
        border: 1px solid #00558e;
        border-radius: 5px;
        padding: 2px;
        display: inline-block;
        height: 21px;
        width: 34px;
        line-height: 2;
        padding-left: 11px;
    }
    .msg a:nth-of-type(1) {
        text-decoration: none;
        color: #00558e;
        font-size: 12px;
        border: 1px solid #00558e;
        border-radius: 5px;
        padding: 2px;
        display: inline-block;
        height: 21px;
        width: 34px;
        line-height: 2;
        padding-left: 11px;
    }
    .msg1 span:nth-of-type(2) {
        color: #333;
    }
     .msg2 span:nth-of-type(2) {
        color: #333;
    }
     .msg3 span:nth-of-type(2) {
        color: #333;
    }
     .msg4 span:nth-of-type(2) {
        color: #333;
    }
    
</style>



