import Vue from 'vue'
import Router from 'vue-router'

import money_query from '../project/money_query.vue'
import inforpage from '../project/inforpage.vue'
import detaillist from '../project/detaillist.vue'


Vue.use(Router)

export default new Router({
  model:history,
  routes: [
    {
      path: '/',
      name: 'money_query',
      component: money_query
    },
    {
      path: '/money_query',
      name: 'frominput',
      component: money_query
    },
    {
      path: '/inforpage',
      name: 'inforpage',
      component: inforpage
    },
    {
      path: '/detaillist',
      name: 'detaillist',
      component: detaillist
    },
  ]
})



